import * as constants from "../constants";
import firebase from "firebase/app";
import Cookies from "js-cookie";


export const signInAction = (params) => {
  return dispatch => {
    dispatch({
      type: constants.SIGN_IN
    });
    firebase.auth().signInWithEmailAndPassword(params.email, params.password)
    .then((signInRes) => {
      firebase.auth().currentUser.getIdToken().then(response => {
        dispatch({
          type: constants.SIGN_IN_SUCCESS
        })

        Cookies.set('access-token', response);
        Cookies.set('userId', signInRes.user.uid);
      })
    })
    .catch(error => {
      dispatch({
        type: constants.SIGN_IN_ERROR,
        payload: error
      });
    })
  }
};

export const resetSignInAction = () => {
  return dispatch => {
    dispatch({
      type: constants.RESET_SIGN_IN
    });
  }
};


export const registerAction = (params) => {
  return dispatch => {
    dispatch({
      type: constants.REGISTER
    });
    firebase.auth().createUserWithEmailAndPassword(params.email, params.password)
    .then((regRes) => {
      firebase.auth().currentUser.getIdToken().then(response => {
        const uid = regRes.user.uid;

        Cookies.set('access-token', response);
        Cookies.set('userId', uid);

        firebase.database().ref(`/users/${uid}/info`).set({
          id: uid,
          name: params.name
        })
        .then(() => {
          dispatch({
            type: constants.REGISTER_SUCCESS
          })
        })
      })
    })
    .catch(error => {
      dispatch({
        type: constants.REGISTER_ERROR,
        payload: error
      });
    })
  }
};

export const resetRegisterAction = () => {
  return dispatch => {
    dispatch({
      type: constants.RESET_REGISTER
    });
  }
};


export const logOutAction = () => {
  return dispatch => {
    dispatch({
      type: constants.LOG_OUT
    });
    firebase.auth().signOut()
    .then(() => {
      dispatch({
        type: constants.LOG_OUT_SUCCESS
      })

      Cookies.remove('access-token');
      Cookies.remove('userId');
    })
    .catch(error => {
      dispatch({
        type: constants.LOG_OUT_ERROR,
        payload: error
      });
    })
  }
};

export const resetLogOutAction = () => {
  return dispatch => {
    dispatch({
      type: constants.RESET_LOG_OUT
    });
  }
};

