import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Spin } from "antd";
import Cookies from "js-cookie";

import * as actions from "../../actions";

import { AddWordModal } from "../../organisms/add-word-modal";

const userId = Cookies.get('userId');

export const Folder = (props) => {
  const mainStore = useSelector(state => state.get('main'));

  const categoryId = props.match.params.id;

  const dispatch = useDispatch();

  const { currentCategory } = mainStore;

  const [ addWordModalProps, setAddWordModalProps ] = useState({
    visible: false,
    shouldRender: false,
    categoryId: null
  });


  useEffect(() => {
    dispatch(
      actions.getCurrentCategory({
        userId,
        categoryId
      })
    )
  }, [])

  const onRefreshPage = () => {
    dispatch(
      actions.getCurrentCategory({
        userId,
        categoryId
      })
    )
  }

  return (
    <div className="category-page">
      <Button onClick={onRefreshPage}>Обновить</Button>
      {currentCategory.data ?
        <>
          <div className="category-page__head">
            <h1>Категория "{currentCategory.data.name}"</h1>
            <Button
              type="primary"
              onClick={() => setAddWordModalProps({ visible: true, shouldRender: true, categoryId })}
            >
              Добавить слово
            </Button>
          </div>
          {currentCategory.data.words &&
          <div className="words">
            {Object.keys(currentCategory.data.words).map((item) => {
              return <div className="words__item" key={item}>
                <div className="words__item__word">
                  {currentCategory.data.words[item].wordName}
                </div>
                <div className="words__item__word">
                  {currentCategory.data.words[item].wordTranslate}
                </div>
              </div>
            })}
          </div>
          }
          {addWordModalProps.shouldRender && <AddWordModal
            modalProps={addWordModalProps} setModalProps={setAddWordModalProps}
          />}
        </>
        :
        <div className="abs-loader">
          <Spin size="large"/>
        </div>
      }
    </div>
  )
};