import produce from "immer";
import * as constants from "../constants";

const initState = {
  signIn: {
    loading: false,
    success: false,
    error: null
  },
  register: {
    loading: false,
    success: false,
    error: null
  },
  logOut: {
    loading: false,
    success: false,
    error: null
  }
};

export const authReducer = (state = initState, action) => {
  switch (action.type) {
    case constants.SIGN_IN:
      return produce(state, draftState => {
        draftState.signIn.loading = true;
      })
    case constants.SIGN_IN_SUCCESS:
      return produce(state, draftState => {
        draftState.signIn = {
          loading: false,
          success: true,
          error: null
        }
      })
    case constants.SIGN_IN_ERROR:
      return produce(state, draftState => {
        draftState.signIn = {
          loading: false,
          success: false,
          error: action.payload
        }
      })
    case constants.RESET_SIGN_IN:
      return produce(state, draftState => {
        draftState.signIn = {
          loading: false,
          success: false,
          error: null
        }
      })
    case constants.REGISTER:
      return produce(state, draftState => {
        draftState.register.loading = true;
      })
    case constants.REGISTER_SUCCESS:
      return produce(state, draftState => {
        draftState.register = {
          loading: false,
          success: true,
          error: null
        }
      })
    case constants.REGISTER_ERROR:
      return produce(state, draftState => {
        draftState.register = {
          loading: false,
          success: false,
          error: action.payload
        }
      })
    case constants.RESET_REGISTER:
      return produce(state, draftState => {
        draftState.register = {
          loading: false,
          success: false,
          error: null
        }
      })
    case constants.LOG_OUT:
      return produce(state, draftState => {
        draftState.logOut.loading = true;
      })
    case constants.LOG_OUT_SUCCESS:
      return produce(state, draftState => {
        draftState.logOut = {
          loading: false,
          success: true,
          error: null
        }
      })
    case constants.LOG_OUT_ERROR:
      return produce(state, draftState => {
        draftState.logOut = {
          loading: false,
          success: false,
          error: action.payload
        }
      })
    case constants.RESET_LOG_OUT:
      return produce(state, draftState => {
        draftState.logOut = {
          loading: false,
          success: false,
          error: null
        }
      })
    default:
      return state;
  }
}