import produce from "immer";
import * as constants from "../constants";

const initState = {
  userInfo: {
    loading: false,
    data: null,
    error: null
  },
  addCategory: {
    loading: false,
    success: null,
    error: null
  },
  categories: {
    loading: false,
    data: [],
    error: null
  },
  currentCategory: {
    loading: false,
    data: null,
    error: null
  },
  addWord: {
    loading: false,
    success: null,
    error: null
  }
};

export const mainReducer = (state = initState, action) => {
  switch (action.type) {
    case constants.GET_USER_INFO:
      return produce(state, draftState => {
        draftState.userInfo.loading = true;
      })
    case constants.GET_USER_INFO_SUCCESS:
      return produce(state, draftState => {
        draftState.userInfo = {
          loading: false,
          data: action.payload,
          error: null
        }
      })
    case constants.GET_USER_INFO_ERROR:
      return produce(state, draftState => {
        draftState.userInfo = {
          loading: false,
          data: null,
          error: action.payload
        }
      })
    case constants.GET_CATEGORIES:
      return produce(state, draftState => {
        draftState.categories.loading = true;
      })
    case constants.GET_CATEGORIES_SUCCESS:
      return produce(state, draftState => {
        draftState.categories = {
          loading: false,
          data: action.payload,
          error: null
        }
      })
    case constants.GET_CATEGORIES_ERROR:
      return produce(state, draftState => {
        draftState.categories = {
          loading: false,
          data: [],
          error: action.payload
        }
      })
    case constants.GET_CURRENT_CATEGORY:
      return produce(state, draftState => {
        draftState.currentCategory.loading = true;
      })
    case constants.GET_CURRENT_CATEGORY_SUCCESS:
      return produce(state, draftState => {
        draftState.currentCategory = {
          loading: false,
          data: action.payload,
          error: null
        }
      })
    case constants.GET_CURRENT_CATEGORY_ERROR:
      return produce(state, draftState => {
        draftState.currentCategory = {
          loading: false,
          data: null,
          error: action.payload
        }
      })
    case constants.ADD_CATEGORY:
      return produce(state, draftState => {
        draftState.addCategory.loading = true;
      })
    case constants.ADD_CATEGORY_SUCCESS:
      return produce(state, draftState => {
        draftState.addCategory = {
          loading: false,
          success: true,
          error: null
        }
      })
    case constants.ADD_CATEGORY_ERROR:
      return produce(state, draftState => {
        draftState.addCategory = {
          loading: false,
          success: false,
          error: action.payload
        }
      })
    case constants.RESET_ADD_CATEGORY:
      return produce(state, draftState => {
        draftState.addCategory = {
          loading: false,
          success: false,
          error: null
        }
      })
    case constants.ADD_WORD:
      return produce(state, draftState => {
        draftState.addWord.loading = true;
      })
    case constants.ADD_WORD_SUCCESS:
      return produce(state, draftState => {
        draftState.addWord = {
          loading: false,
          success: true,
          error: null
        }
      })
    case constants.ADD_WORD_ERROR:
      return produce(state, draftState => {
        draftState.addWord = {
          loading: false,
          success: false,
          error: action.payload
        }
      })
    case constants.RESET_ADD_WORD:
      return produce(state, draftState => {
        draftState.addWord = {
          loading: false,
          success: false,
          error: null
        }
      })
    default:
      return state;
  }
}