import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { Menu, Spin, Button } from "antd";
import Cookies from "js-cookie";

import * as actions from "../actions";

import { AddCategoryModal } from "../organisms/add-category-modal";

const userId = Cookies.get('userId');

export const Categories = () => {
  const mainStore = useSelector(state => state.get('main'));

  const dispatch = useDispatch();

  const { categories } = mainStore;

  const [ addCategoryModalProps, setAddCategoryModalProps ] = useState({
    visible: false,
    shouldRender: false
  });

  useEffect(() => {
    dispatch(
      actions.getCategoriesAction({ userId })
    )
  }, []);

  return (
    <>
      {categories.loading && <div className="abs-loader"><Spin size="large"/></div>}
      <div className="add-category-btn">
        <Button type="primary" onClick={() => setAddCategoryModalProps({ visible: true, shouldRender: true })}>
          Добавить категорию +
        </Button>
      </div>
      <Menu mode="inline" style={{ borderRight: 0 }}>
        {categories.data.map((item) => <Menu.Item key={item.id}><NavLink to={`/folder/${item.id}`}>{item.name}</NavLink></Menu.Item>)}
      </Menu>
      {addCategoryModalProps.shouldRender && <AddCategoryModal
        modalProps={addCategoryModalProps} setModalProps={setAddCategoryModalProps}
      />}
    </>
  );
}
