import * as constants from "../constants";
import firebase from "firebase/app";


export const getUserInfoAction = (params) => {
  return dispatch => {
    dispatch({
      type: constants.GET_USER_INFO
    });
    firebase.database().ref(`/users/${params.userId}/info`).once('value')
    .then((response) => {
      dispatch({
        type: constants.GET_USER_INFO_SUCCESS,
        payload: response.val()
      });
    })
    .catch(error => {
      dispatch({
        type: constants.GET_USER_INFO_ERROR,
        payload: error
      });
    })
  }
};

export const getCategoriesAction = (params) => {
  return dispatch => {
    dispatch({
      type: constants.GET_CATEGORIES
    });
    firebase.database().ref(`/users/${params.userId}/categories-list`).once('value')
    .then((response) => {
      const categories = Object.keys(response.val()).map((key) => {
        return {
          id: key,
          ...response.val()[key]
        }
      })

      dispatch({
        type: constants.GET_CATEGORIES_SUCCESS,
        payload: categories
      });
    })
    .catch(error => {
      dispatch({
        type: constants.GET_CATEGORIES_ERROR,
        payload: error
      });
    })
  }
};

export const getCurrentCategory = (params) => {
  return dispatch => {
    dispatch({
      type: constants.GET_CURRENT_CATEGORY
    });
    firebase.database().ref(`/users/${params.userId}/categories`).child(params.categoryId).once('value')
    .then((response) => {
      dispatch({
        type: constants.GET_CURRENT_CATEGORY_SUCCESS,
        payload: response.val()
      });
    })
    .catch(error => {
      dispatch({
        type: constants.GET_CURRENT_CATEGORY_ERROR,
        payload: error
      });
    })
  }
}

export const addCategoryAction = ({ userId, ...params }) => {
  return dispatch => {
    dispatch({
      type: constants.ADD_CATEGORY
    });
    firebase.database().ref(`/users/${userId}/categories-list`).push(params)
    .then((res) => {
      firebase.database().ref(`/users/${userId}/categories/${res.key}`).set(params).then(() => {
        dispatch({
          type: constants.ADD_CATEGORY_SUCCESS
        });
      })
    })
    .catch(error => {
      dispatch({
        type: constants.ADD_CATEGORY_ERROR,
        payload: error
      });
    })
  }
};

export const resetAddCategoryAction = () => {
  return dispatch => {
    dispatch({
      type: constants.RESET_ADD_CATEGORY
    });
  }
};

export const addWordAction = ({ userId, categoryId, ...params }) => {
  return dispatch => {
    dispatch({
      type: constants.ADD_WORD
    });
    firebase.database().ref(`/users/${userId}/categories`).child(categoryId).child("words").push(params)
    .then(() => {
      dispatch({
        type: constants.ADD_WORD_SUCCESS
      });
    })
    .catch(error => {
      dispatch({
        type: constants.ADD_WORD_ERROR,
        payload: error
      });
    })
  }
};

export const resetAddWordAction = () => {
  return dispatch => {
    dispatch({
      type: constants.RESET_ADD_WORD
    });
  }
};