import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Row, Col, Modal, Button, Form, Input, notification, Alert, Spin } from "antd";

import * as actions from "../actions";

import { FormField } from "../../../ui/molecules/form-field";

export const AddCategoryModal = (props) => {
  const mainStore = useSelector(state => state.get('main'));

  const dispatch = useDispatch();

  const { userInfo, addCategory } = mainStore;

  const { modalProps, setModalProps } = props;

  const [formFields, setFormFields] = useState({
    userId: userInfo.data.id
  });
  const [fieldsErrors, setFieldsErrors] = useState({});

  useEffect(() => {
    if (addCategory.success) {
      notification['success']({
        message: "Категория добавлена",
      });
      closeModal();

      dispatch(
        actions.resetAddCategoryAction()
      )

      dispatch(
        actions.getCategoriesAction({ userId: userInfo.data.id })
      )
    }
  }, [ addCategory.success ]);


  const closeModal = () => {
    setModalProps({ ...modalProps, visible: false });
  };

  const afterClose = () => {
    setModalProps({ ...modalProps, shouldRender: false });
  };


  const onFormFieldChange = (prop, val) => {
    setFormFields({
      ...formFields,
      [prop]: val
    });
  };

  const validateForm = () => {
    const notFilledMessage = "Не заполнено поле";

    const errors = {};

    if (!formFields.name) errors.name = notFilledMessage;

    return errors;
  };

  const onSubmit = () => {
    const errors = validateForm();

    if (Object.keys(errors).length) {
      setFieldsErrors(errors);
      return;
    }

    setFieldsErrors({});

    dispatch(
      actions.addCategoryAction(formFields)
    )
  };

  return (
    <Modal
      className="custom-modal"
      title="Добавить категорию"
      visible={modalProps.visible}
      onCancel={closeModal}
      afterClose={afterClose}
      footer={null}
      width={480}
    >
      {addCategory.error && <div className="m-b-20">
        <Alert message={addCategory.error.message} type="error"/>
      </div>}
      {addCategory.loading && <div className="modal-loader">
        <Spin size="large"/>
      </div>}
      <Form onFinish={onSubmit}>
        <FormField title="Название категории" error={fieldsErrors.name}>
          <Input
            size="large"
            placeholder="Введите название категории"
            type="text"
            value={formFields.name}
            onChange={(e) => onFormFieldChange("name", e.target.value)}
          />
        </FormField>
        <Row className="custom-modal__button-row" type="flex" gutter={[ 24, 0 ]}>
          <Col span={12}>
            <Button size="large" type="ghost" onClick={closeModal} className="full-width">Отмена</Button>
          </Col>
          <Col span={12}>
            <Button size="large" type="primary" htmlType="submit" className="full-width">Добавить</Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};