import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../actions";

export const SignIn = (props) => {
  const authStore = useSelector(state => state.get('auth'));

  const dispatch = useDispatch();

  const { signIn } = authStore;

  const [ formFields, setFormFields ] = useState({});

  useEffect(() => {
    if (signIn.success) {
      dispatch(
        actions.resetSignInAction()
      )
      props.history.push('/')
    }
  }, [ props.history, signIn.success ])

  const onFormFieldChange = (prop, val) => {
    setFormFields({
      ...formFields,
      [prop]: val
    })
  };

  const onSignIn = () => {
    dispatch(
      actions.signInAction(formFields)
    )
  };

  return (
    <div>
      {signIn.loading && <div>...LOADING</div>}
      {signIn.error && <div className="auth__error">{signIn.error.message}</div>}
      <div>
        <div>Login</div>
        <input
          type="text"
          onChange={(e) => onFormFieldChange("email", e.target.value)}
        />
      </div>
      <div>
        <div>Password</div>
        <input
          type="password"
          onChange={(e) => onFormFieldChange("password", e.target.value)}
        />
      </div>
      <div>
        <button onClick={onSignIn}>Войти</button>
      </div>
    </div>
  )
}