import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Row, Col, Modal, Button, Form, Input, notification, Alert, Spin } from "antd";

import * as actions from "../actions";

import { FormField } from "../../../ui/molecules/form-field";

export const AddWordModal = (props) => {
  const mainStore = useSelector(state => state.get('main'));

  const dispatch = useDispatch();

  const { userInfo, addWord } = mainStore;

  const { modalProps, setModalProps } = props;

  const [formFields, setFormFields] = useState({
    userId: userInfo.data.id,
    categoryId: modalProps.categoryId
  });
  const [fieldsErrors, setFieldsErrors] = useState({});


  useEffect(() => {
    if (addWord.success) {
      notification['success']({
        message: "Слово добавлено",
      });
      closeModal();

      dispatch(
        actions.resetAddWordAction()
      )
    }
  }, [addWord.success]);


  const closeModal = () => {
    setModalProps({ ...modalProps, visible: false });
  };

  const afterClose = () => {
    setModalProps({ ...modalProps, shouldRender: false, categoryId: null });
  };


  const onFormFieldChange = (prop, val) => {
    setFormFields({
      ...formFields,
      [prop]: val
    });
  };

  const validateForm = () => {
    const notFilledMessage = "Не заполнено поле";

    const errors = {};

    if (!formFields.wordName) errors.wordName = notFilledMessage;
    if (!formFields.wordTranslate) errors.wordTranslate = notFilledMessage;

    return errors;
  };

  const onSubmit = () => {
    const errors = validateForm();

    if (Object.keys(errors).length) {
      setFieldsErrors(errors);
      return;
    }

    setFieldsErrors({});

    dispatch(
      actions.addWordAction(formFields)
    )
  };

  return (
    <Modal
      className="custom-modal"
      title="Добавить слово"
      visible={modalProps.visible}
      onCancel={closeModal}
      afterClose={afterClose}
      footer={null}
      width={480}
    >
      {addWord.error && <div className="m-b-20">
        <Alert message={addWord.error.message} type="error"/>
      </div>}
      {addWord.loading && <div className="modal-loader">
        <Spin size="large"/>
      </div>}
      <Form onFinish={onSubmit}>
        <FormField title="Слово" error={fieldsErrors.wordName}>
          <Input
            size="large"
            placeholder="Введите слово"
            type="text"
            value={formFields.wordName}
            onChange={(e) => onFormFieldChange("wordName", e.target.value)}
          />
        </FormField>
        <FormField title="Перевод" error={fieldsErrors.wordTranslate}>
          <Input
            size="large"
            placeholder="Введите перевод"
            type="text"
            value={formFields.wordTranslate}
            onChange={(e) => onFormFieldChange("wordTranslate", e.target.value)}
          />
        </FormField>
        <Row className="custom-modal__button-row" type="flex" gutter={[ 24, 0 ]}>
          <Col span={12}>
            <Button size="large" type="ghost" onClick={closeModal} className="full-width">Отмена</Button>
          </Col>
          <Col span={12}>
            <Button size="large" type="primary" htmlType="submit" className="full-width">Добавить</Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};