import React from "react";
import { Route, BrowserRouter, Switch } from 'react-router-dom';

import { Auth } from "./screens/auth";
import { Main } from "./screens/main";

const App = () => {
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route path='/auth' component={Auth} />
          <Route path='/' component={Main} />
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
