import React from "react";

export const FormField = (props) => {
  const { title, children, error, className} = props;

  return (
    <div className={`form-field-item ${className ? className : ''} ${error ? 'form-field-item-error ant-form-item-has-error' : ''}`}>
      {title && <div className="form-field-item__title">{title}</div>}
      {children}
      {error && <div className="form-field-item__error">{error}</div>}
    </div>
  );
};