import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../actions";

export const Register = (props) => {
  const authStore = useSelector(state => state.get('auth'));

  const dispatch = useDispatch();

  const { register } = authStore;

  const [ formFields, setFormFields ] = useState({});

  useEffect(() => {
    if (register.success) {
      dispatch(
        actions.resetRegisterAction()
      )
      props.history.push('/')
    }
  }, [ props.history, register.success ])

  const onFormFieldChange = (prop, val) => {
    setFormFields({
      ...formFields,
      [prop]: val
    })
  };

  const onRegister = () => {


    dispatch(
      actions.registerAction(formFields)
    )
  };

  return (
    <div>
      {register.loading && <div>...LOADING</div>}
      {register.error && <div className="auth__error">{register.error.message}</div>}
      <div>
        <div>Name</div>
        <input
          type="text"
          onChange={(e) => onFormFieldChange("name", e.target.value)}
        />
      </div>
      <div>
        <div>Login</div>
        <input
          type="text"
          onChange={(e) => onFormFieldChange("email", e.target.value)}
        />
      </div>
      <div>
        <div>Password</div>
        <input
          type="password"
          onChange={(e) => onFormFieldChange("password", e.target.value)}
        />
      </div>
      <div>
        <button onClick={onRegister}>Зарегистрироваться</button>
      </div>
    </div>
  )
}