import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { Dropdown, Button } from 'antd';

import * as authActions from "../../auth/actions";

export const CurrentUser = () => {
  const authStore = useSelector(state => state.get('auth'));
  const mainStore = useSelector(state => state.get('main'));

  const dispatch = useDispatch();

  const history = useHistory();

  const { logOut } = authStore;
  const { userInfo } = mainStore;


  useEffect(() => {
    if (logOut.success) {
      dispatch(
        authActions.resetLogOutAction()
      )
      history.push('/auth/sign-in');
    }
  }, [ history, logOut.success ])

  const onlogOut = () => {
    dispatch(
      authActions.logOutAction()
    )
  }

  const userOverlay = <div className="dropdown-menu">
    <div className="dropdown-menu-title">{userInfo.data ? userInfo.data.name : ""}</div>
    <Button onClick={onlogOut} type="primary" size="large">Log out</Button>
  </div>

  return (
    <Dropdown overlay={userOverlay} trigger={['click']} placement="bottomRight" arrow>
      <Button type="primary" size="large">
        USER
      </Button>
    </Dropdown>
  );
}
